.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: steps(60, end) infinite minute-hand-animation;
  position: absolute;
  bottom: 50%;
}

.stopwatch__second-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: linear infinite second-hand-animation;
  position: absolute;
  bottom: 50%;
}

.stopwatch__center-circle {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: calc(50% - 2.5vmin);
  left: calc(50% - 2.5vmin);
}

@keyframes second-hand-animation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes minute-hand-animation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up.stopwatch .stopwatch__second-hand {
  animation-duration: 10s;
}

.stopwatch--speed-up.stopwatch .stopwatch__minute-hand {
  animation-duration: 600s;
}

body, html {
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

